import React, { Component } from "react";
// /* import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import My_router from "../../../../router/My_router";
import Navigator from '../../../navigator/navigator.js';

class App extends Component {
  render() {
    return (
      <main className="page-content faq-page">
        <div className="layout">
          <div className="layout-mid">
            <Navigator
              url_arey={[{
                  to: My_router.url(""),
                  lun_text: My_router.stor.page[My_router.stor.lang].faq.r1
                },{
                  to: My_router.url("/faq"),
                  lun_text: My_router.stor.page[My_router.stor.lang].faq.r2
                }
              ]}
              className="breadcrumbs mobile-padding"
              span={My_router.stor.page[My_router.stor.lang].faq.r29}
            />
            <div className="page-title mobile-padding" data-pageame="faq.r8">
              {My_router.stor.page[My_router.stor.lang].faq.r29 }
            </div>
          </div>
        </div>
        <div>
        {this.props.data}
        </div>
        
      </main>
    );
  }
}



export default App;



